import * as React from 'react';
import { createStore, Reducer } from 'redux';
import { Provider } from 'react-redux';

import './Game.css';
import GameBoard from './ReduxStore/containers/GameBoard';
import SettingsControl from './ReduxStore/containers/SettingsControl';

import reducer from './ReduxStore/reducers/GameReducer';
import { GameState } from './types/objectTypes';
import { initialState } from './initialState'

const store = createStore<GameState>(reducer as Reducer<GameState>, initialState);

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>Welcome to Color Wars</h2>
        </div>
        <Provider store={store}>
        <GameBoard />
        </Provider>
        <Provider store={store}>
        <SettingsControl />
        </Provider>
      </div>
    );
  }
}

export default App;



