import { fieldsAction, dimensionAction, playersAction } from '../actionTypes';
import { BoardField, Point, Player } from '../../types/objectTypes';
import * as constants from '../constants';

export function fieldsReducer(state: BoardField[] = [], action: fieldsAction): BoardField[] {
    //make copy of state
    var newFields: BoardField[] = state.slice();
    
    //modify board
    switch (action.type) {
        case constants.ADD_FIELD:
            newFields.push({ownerName: action.owner, coords: action.coords})

        case constants.REMOVE_FIELD:
            newFields = removeField(newFields, action.coords);
        
        case constants.CLAIM_FIELD:
        var newField: BoardField | undefined = findFieldByCoords(newFields, action.coords);
        if (newField !== undefined){
            newField.ownerName = action.owner;
            newFields = removeField(newFields, action.coords);
            newFields.push(newField);
        }
    }
    return newFields;
}

export function dimensionReducer(state: Point = {X: 0, Y: 0}, action: dimensionAction): Point {

    //modify board
    switch (action.type) {
        case constants.SET_DIMENSION:
            var newPoint: Point = action.coords;
            return newPoint;
    }
    return state;
}

export function playersReducer(state: Player[] = [], action: playersAction): Player[] {
    
        //TODO
        return state;
    }
    

//helpers

function removeField(fields: BoardField[], coords: Point) {
    return fields.filter(function(field: BoardField) {
        return field.coords !== coords;
    });
}

function findFieldByCoords(fields: BoardField[], coords: Point){
    return fields.find(function(field: BoardField) {
        return field.coords == coords;            
    });
}


