import * as React from 'react';
import * as Konva from 'react-konva';

export interface Props {
  color: string;
  rect: {
    X: number,
    Y: number,
    Width: number,
    Height: number
  };
}

function TailComponent({ color, rect }: Props) {
  return (
    <Konva.Rect
      x={rect.X}
      y={rect.Y}
      width={rect.Width}
      height={rect.Height}
      fill={color}
      shadowBlur={3}
      opacity={0.3}
    />
  );
}

export default TailComponent;


