import * as React from 'react';
import {Stage, Layer} from 'react-konva';

import { Point, BoardField, BoardFieldVisual, Player } from '../../types/objectTypes';
import BoardFieldComponent from './GameElements/BoardVisual';
import PlayerComponent from './GameElements/PlayerVisual';
import TailComponent from './GameElements/TailVisual';

export interface Props {
    fields?: BoardField[],
    dimension?: Point,
    players?: Player[]
}

function GameBoard({fields = [], dimension = {X:0, Y:0}, players = []}: Props) {
    //temporarily hardcoded field size in pixels
    var canvasDimension: Point = {X:dimension.X*30,Y:dimension.Y*30};
    var fieldVisuals: BoardFieldVisual[] = createBoardFieldVisuals(fields, players);
    const fieldComponents = fieldVisuals.map((curField: BoardFieldVisual) =>
      <BoardFieldComponent color={curField.color} rect={layouter(dimension, canvasDimension, curField.coords)} />
    );
    const playerComponents = players.map((curPlayer: Player) =>
    <PlayerComponent color={curPlayer.color} rect={layouter(dimension, canvasDimension, curPlayer.coords)} />
    );
    const tailComponents = players.map((curPlayer: Player) =>
      curPlayer.tail.map((p: Point) => <TailComponent color={curPlayer.color} rect={layouter(dimension, canvasDimension, p)} />)
    );

    return (
      <div className="App">
        <Stage width={canvasDimension.X} height={canvasDimension.Y} >
        <Layer>
          {fieldComponents}
        </Layer>
        <Layer>
          {tailComponents}
          {playerComponents}
        </Layer>
      </Stage>
      </div>
    );
}

export default GameBoard;

// helpers

function layouter(boardDimension: Point,
                  canvasDimension: Point,
                  fieldLocation: Point) {
  return{
          X: canvasDimension.X/boardDimension.X*fieldLocation.X,
          Y: canvasDimension.Y/boardDimension.Y*fieldLocation.Y,
          Width: canvasDimension.X/boardDimension.X,
          Height: canvasDimension.Y/boardDimension.Y
        };
}

// helpers

function createBoardFieldVisuals(fields: BoardField[], players: Player[]): BoardFieldVisual[]{
  var newFields: BoardFieldVisual[] = [];
  //get owner color
  fields.forEach((field: BoardField) => {
    var owner: Player | undefined = players.find((player) => player.name === field.ownerName);
    var color: string = (owner === undefined ? 'white' : color = owner.color);
    var newField: BoardFieldVisual = {color, coords: field.coords}; 
    newFields.push(newField);  
});
  return newFields;
}

