import SettingsControl from '../../ReactView/components/SettingsComponent';
import * as actions from '../actionTypes';
import { connect, Dispatch } from 'react-redux';

import { Point, GameState } from '../../types/objectTypes'

export function mapStateToProps(state: GameState) {
  return {
      state
  }
}

export function mapDispatchToProps(dispatch: Dispatch<actions.boardAction>) {
    return {
      onAdd: (coords: Point, owner: string) => dispatch(actions.addField(coords, owner)),
      onDelete: (coords: Point) => dispatch(actions.removeField(coords)),
      onClaim: (coords: Point, owner: string) => dispatch(actions.claimField(coords, owner)),
      onChangeDimension: (coords: Point) => dispatch(actions.setDimension(coords))
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(SettingsControl);


