import * as React from 'react';
import * as Konva from 'react-konva';

export interface Props {
  color: string;
  rect: {
    X: number,
    Y: number,
    Width: number,
    Height: number
  };
}

function BoardFieldComponent({ color, rect }: Props) {
  return (
    <Konva.Rect
      x={rect.X}
      y={rect.Y}
      width={rect.Width}
      height={rect.Height}
      fill={color}
      shadowBlur={color === 'white' ? 0 : 2}
      opacity={color === 'white' ? 0 : 0.5}
    />
  );
}

export default BoardFieldComponent;


