import * as constants from './constants'
import { Point } from '../types/objectTypes'

export interface addField {
    type: constants.ADD_FIELD,
    coords: Point,
    owner: string
}

export interface removeField {
    type: constants.REMOVE_FIELD,
    coords: Point,
    owner: string
}

export interface claimField {
    type: constants.CLAIM_FIELD,
    coords: Point,
    owner: string
}

export interface setDimension {
    type: constants.SET_DIMENSION,
    coords: Point
}

export interface addPlayer {
    type: constants.ADD_PLAYER,
    name: string,
    color: string
}

export interface removePlayer {
    type: constants.REMOVE_PLAYER,
    name: string,
    color: string    
}

export type fieldsAction = claimField | addField | removeField;
export type dimensionAction = setDimension;
export type playersAction = addPlayer | removePlayer;
export type boardAction = fieldsAction | dimensionAction;

export function addField(coords: Point, owner: string): addField {
    return {
        type: constants.ADD_FIELD,
        coords: coords,
        owner: owner
        
    }
}

export function removeField(coords: Point, owner: string = ''): removeField {
    return {
        type: constants.REMOVE_FIELD,
        coords: coords,
        owner: owner
    }
}

export function claimField(coords: Point, owner: string): claimField {
    return {
        type: constants.CLAIM_FIELD,
        coords: coords,
        owner: owner
    }
}

export function setDimension(coords: Point): setDimension {
    return {
        type: constants.SET_DIMENSION,
        coords: coords
    }
}

export function addPlayer(name: string, color: string): addPlayer {
    return {
        type: constants.ADD_PLAYER,
        name: name,
        color: color 
    }
}

export function removePlayer(name: string, color: string = ''): removePlayer {
    return {
        type: constants.REMOVE_PLAYER,
        name: name,
        color: color 

    }
}



