import GameBoard from '../../ReactView/components/GameBoard';
import { GameState } from '../../types/objectTypes';
import { connect } from 'react-redux';

export function mapStateToProps({ board: {fields: fields, dimension: dimension}, players: players}: GameState) {
    return {
        fields,
        dimension,
        players
    }
}

export default connect(mapStateToProps)(GameBoard);


