import * as React from 'react';

import { Point, GameState } from '../../types/objectTypes'

export interface Props {
    state: GameState,
    onAdd: (coords: Point, owner: string) => void,
    onDelete: (coords: Point) => void,
    onClaim: (coords: Point, owner: string) => void,
    onChangeDimension: (coords: Point) => void
}

function SettingsComponent({state, onAdd, onDelete, onClaim, onChangeDimension}: Props) {
      var state: GameState = state;
    return (
      <div className="App">
      </div>
    );
}

export default SettingsComponent;


